import axios from "axios";
import { useState } from "react";
import { useNavigate } from "react-router-dom"; // Importez useNavigate
import {API} from "../services/API";

const Inscription = () => {
  const [username, setUsername] = useState('');
  // username : La premire valeur du tableau est la valeur actuelle de l'tat.  l'initialisation, il s'agit de la valeur que vous avez fournie ('' dans cet exemple).
  // setUsername : La deuxime valeur est une fonction que vous pouvez appeler pour modifier cet tat. Lorsque vous voulez mettre  jour la valeur de username, vous appelez setUsername avec une nouvelle valeur.
  const [password, setPassword] = useState('');
  const [email, setEmail] = useState('');
  const navigate = useNavigate(); // Initialisation de useNavigate

  const register = async () => {
    if (!username.trim() || !email.trim() || !password) {
      alert('Merci de bien remplir tous les champs.');
      return;
    }
    try {
      const response = await API.post('/inscription', {
        // trim() est utilis pour supprimer les espaces blancs avant et aprs les valeurs des champs, pour s'assurer que l'utilisateur n'a pas entr que des espaces.
        username: username.trim(),
        email: email.trim(),
        password: password
      });
      console.log(response.data);
      navigate('/login'); // Redirection vers la page '/login' en supposant que l'inscription a russi.
    } catch (error) {
      console.error(error);
      alert("L'inscription a chou: " + error.response?.data || 'Erreur inconnu');
    }
  };

  return (
    <div className="signin">
      <h1 className="loginTitle">Inscrivez-vous</h1>
      <div className="wrapper_inscription">
        <div className="center_inscription">
          <input 
            type="text" 
            placeholder="Identifiant" 
            onChange={(e) => setUsername(e.target.value)} 
            // Lorsque l'utilisateur saisit un texte dans le champ input, l'vnement onChange est dclench
            // Lorsque setUsername est appele, React change la valeur de username dans l'tat local du composant  la valeur que l'utilisateur a entre. React dtecte ensuite que l'tat a chang.
          />
          <input 
            type="email"  // Type chang en email
            placeholder="Email" 
            onChange={(e) => setEmail(e.target.value)} 
          />
          <input 
            type="password" 
            placeholder="Mot de passe" 
            onChange={(e) => setPassword(e.target.value)} 
          />
          <button className="submit" onClick={register}>S'inscrire</button>
          <p className="sign-up-prompt">Vous avez dj un compte ? <a href="/login">Connectez-vous</a></p>
        </div>
      </div>
    </div>
  );
};

export default Inscription;
